import axios from "axios";
import Qs from "qs";
import { ElMessageBox } from "element-plus";
import { ref } from "vue";

/* eslint-disable @typescript-eslint/no-explicit-any */
const showTips = true;
const serverUrl = "http://121.4.59.60:8081";
const Screen = {
  width: ref(document.documentElement.clientWidth),
};

function storage<T>(key: string) {
  return {
    set(value: T) {
      if (typeof value == "string") {
        localStorage.setItem(key, value);
      } else {
        localStorage.setItem(key, JSON.stringify(value));
      }
    },
    get(): T {
      const sto = localStorage.getItem(key);
      if (sto == null) {
        return {} as unknown as T;
      }
      try {
        return JSON.parse(sto) as T;
      } catch (err) {
        return {} as unknown as T;
      }
    },
    remove() {
      localStorage.removeItem(key);
    },
  };
}

function object<T>(obj: { [index: string]: T }) {
  return {
    filter(func: (value: T) => boolean) {
      const newObj: { [index: string]: T } = {};
      for (const key of Object.keys(obj)) {
        if (func(obj[key])) {
          newObj[key] = obj[key];
        }
      }
      return newObj;
    },
  };
}

function string(str: string) {
  return {
    isEmpty() {
      return str == undefined || str == null || str == "" || str.length == 0;
    },
  };
}

interface MessageBoxConfirmOptions {
  message: string;
  confirm?: () => Promise<unknown>;
  cancel?: () => Promise<unknown>;
  confirmText?: string;
  cancelText?: string;
  titleText?: string;
}

const MessageBox = {
  confirm(options: MessageBoxConfirmOptions) {
    return ElMessageBox({
      title: options.titleText ?? "提示",
      message: options.message,
      showCancelButton: true,
      confirmButtonText: options.confirmText ?? "确定",
      cancelButtonText: options.cancelText ?? "取消",
      beforeClose: (action, instance, done) => {
        if (action === "confirm") {
          instance.confirmButtonLoading = true;
          instance.confirmButtonText = "Loading...";
          (
            options.confirm ??
            function () {
              return new Promise((resolve) => {
                resolve("");
              });
            }
          )().then(() => {
            done();
            instance.confirmButtonLoading = false;
          });
        } else {
          (
            options.cancel ??
            function () {
              return new Promise((resolve) => {
                resolve("");
              });
            }
          )().then(() => {
            done();
          });
        }
      },
    }).catch(() => null);
  },
};

const token = {
  get() {
    return wj.storage<string>("user_token").get();
  },
  set(tk: string) {
    wj.storage<string>("user_token").set(tk);
  },
  del() {
    wj.storage("user_token").remove();
  },
};

type Method =
  | "get"
  | "GET"
  | "delete"
  | "DELETE"
  | "head"
  | "HEAD"
  | "options"
  | "OPTIONS"
  | "post"
  | "POST"
  | "put"
  | "PUT"
  | "patch"
  | "PATCH"
  | "purge"
  | "PURGE"
  | "link"
  | "LINK"
  | "unlink"
  | "UNLINK";
interface requestOptions {
  api: string;
  method?: Method;
  verify?: boolean;
  data?: any;
  data_query?: any;
}
function request(options: requestOptions): Promise<any> {
  if (!options.method) {
    options.method = "POST";
  }
  let data_query_t = "";
  if (options.data_query) {
    data_query_t = "?";
    Object.keys(options.data_query).forEach((key) => {
      data_query_t += key + "=" + options.data_query[key];
    });
  }
  if (options.verify) {
    const token = localStorage.getItem("user_token");
    return axios({
      method: options.method,
      url: serverUrl + options.api + data_query_t,
      headers: {
        Authorization: "Bearer " + token,
      },
      data: Qs.stringify(options.data),
    });
  } else {
    return axios({
      method: options.method,
      url: serverUrl + options.api + data_query_t,
      data: Qs.stringify(options.data),
    });
  }
}

const wj = {
  showTips,
  storage,
  object,
  string,
  request,
  token,
  MessageBox,
  Screen,
};

window.onresize = () => {
  Screen.width.value = document.documentElement.clientWidth;
};

export default wj;
