  } else if (value.length != 11) {
    return callback(new Error("手机号应该为11位"));
  }
  callback();
};

const checkRegisterUsername = (rule: any, value: string, callback: any) => {
  if (!value) {
    return callback(new Error("请输入昵称"));
  }
  callback();
};

const checkRegisterOrganization = (rule: any, value: string, callback: any) => {
  if (!value) {
    return callback(new Error("请输入组织"));
  }
  callback();
};

const checkRegisterPassword = (rule: any, value: string, callback: any) => {
  if (value === "") {
    return callback(new Error("请输入密码"));
  }
  if (registerForm.value.verify !== "") {
