import { ref } from "vue";
import { IUser } from "./models";
import wj from "./wj";
const userinfo = ref({} as IUser);

const User = {
  updata() {
    return wj
      .request({
        method: "GET",
        api: "/info",
        verify: true,
      })
      .then((res) => {
        userinfo.value = res.data.data.user;
      });
  },
  get() {
    return userinfo;
  },
  set(user: IUser) {
    userinfo.value = user;
    wj.storage<IUser>("user_token").set(user);
  },
  logout() {
    wj.storage("user_token").remove();
    userinfo.value = {} as IUser;
  },
};

(function onLoad() {
  User.updata().then(() => {
    console.log(User.get().value);
  });
})();

export { userinfo, User };
